import React from 'react';


export default props => {

    const imgs = props.imgs || [];

    function renderIndicators() {
        return imgs.map((img, index) => (
            <li key={index} data-target="#carousel-example-generic_2" data-slide-to={index} className={index === 0 ? 'active' : ''}></li>
        ))
    }

    function renderImgs() {
        return imgs.map((img, index) => (
            <div key={index} className={index === 0 ? 'item active' : 'item'}>
                <img src={img} alt='' />
                <div className="carousel-caption">

                </div>
            </div>
        ))
    }

    return (
        <div id="carousel-example-generic_2" className="carousel slide" data-interval="false" data-ride="carousel">
            <ol className="carousel-indicators">
                {renderIndicators()}
            </ol>
            <div className="carousel-inner" role="listbox">
                {renderImgs()}
            </div>
            {/* <!-- Controls --> */}
            <a className="left carousel-control" href="#carousel-example-generic_2" role="button" data-slide="prev">
                <span className="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="right carousel-control" href="#carousel-example-generic_2" role="button" data-slide="next">
                <span className="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
        </div>
    )
}



