import React from 'react'

export default props => {

    let star = Math.floor(props.stars);
    let star_half = props.stars - star;
    let all_stars = [];

    for (let index = 0; index < 5; index++) {
        if (star > 0) {
            all_stars.push(<i key={index} className="material-icons">star</i>);
            star--;
        } else {
            if (star_half > 0) {
                all_stars.push(<i key={index} className="material-icons">star_half</i>);
                star_half = 0;
            } else {
                all_stars.push(<i key={index} className="material-icons">star_border</i>);
            }
        }
    }
    return all_stars;
}



