import React, { Component } from 'react';
import CarList from './carlist/CarList';


class Home extends Component {

    render() {
        return (
            <section className="content">
                <div className="row">
                    <div className="block-header card-custom">
                        <h2>
                            Vehicles
                                <small>Find your next vehicle here!</small>
                        </h2>
                    </div>
                    <div className="clearfix">

                        <CarList />

                    </div>
                </div>
            </section>
        );
    }
}

export default Home;



