import React from 'react';
import { Link } from 'react-router-dom';
import Stars from '../../../stars/Stars';

export default props => {

    return (
        <div className="col-lg-4 col-md-4 col-sm-6 col-xs-6">
            <div className='row'>
                <Link to={`/car/${props.id}`}>
                    <div className="card card-custom card-shadow">
                        <img className='img-responsive' src={props.img} alt='' />
                        <div className="body">
                            <div className='card-stars' >
                                <Stars className='card-stars' stars={props.stars} />
                            </div>
                            <h5>{`${props.model} ${props.year}`}</h5>
                            <strong className='pull-right card-value'>{`$ ${props.value}`}</strong>
                            <div className='clearfix'></div>
                        </div>
                    </div>
                </Link>
            </div>
        </div>
    )
}



