import React, { Component } from 'react';
import consts from '../../consts';
import axios from 'axios';
import Carousel from '../carousel/Carousel'
import Stars from '../stars/Stars';

const BASE_URL = consts.API_URL;


export default class CarItem extends Component {

    constructor(props) {
        super(props)
        this.state = {
            vehicle: '',
            vehicleFiles: '',
            car: '',
        }
    }


    getVehicle() {
        axios.get(`${BASE_URL}/vehicles/${this.props.match.params.carId}`)
            .then(res => {
                this.setState({
                    vehicle: res.data
                })
                this.getVehicleFiles();
            })
    }

    getVehicleFiles() {
        axios.get(`${BASE_URL}/containers/${this.state.vehicle.id}/files`)
            .then(res => {
                this.setState({
                    vehicleFiles: res.data
                })
                this.getVehicleImgs();
            })
    }

    getVehicleImgs() {
        let imgs = [];
        this.state.vehicleFiles.map((img, index) => (
            imgs = [...imgs, `${BASE_URL}/containers/${this.state.vehicle.id}/download/${img.filename}`]
        ))
        this.setState({
            car: { ...this.state.vehicle, imgs }
        })
    }

    renderOptions(options = []) {
        return options.map((option, index) => (
            <li key={index}>{option.name}</li>
        ))
    }

    componentWillMount() {
        this.getVehicle();
    }

    render() {
        return (
            <section className="content">
                <div className="row">
                    <div className="clearfix">
                        <div className="col-lg-8 col-md-8 col-sm-12 col-xs-12" >
                            <div className='row'>
                                <div className="card card-custom">
                                    <Carousel imgs={this.state.car.imgs} />
                                    <div className="body">
                                        <div className='card-stars' >
                                            <Stars stars={this.state.car.stars} />
                                        </div>
                                        <h5>{this.state.car.model} {this.state.car.year}</h5>
                                        <strong className='pull-right card-value'>{`$ ${this.state.car.price}`}</strong>
                                        <div className='clearfix'></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12" >
                            <div className='row'>
                                <div className='card-custom'>
                                    <button onClick={() => this.props.history.goBack()} type="button" className="btn btn-block btn-lg bg-red waves-effect">
                                        <i className="material-icons">navigate_before</i>
                                        <span> RETURN TO LIST</span>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12" >
                            <div className='row'>
                                <div className="card-custom">
                                    <div className="panel-group" id="accordion_19" role="tablist" aria-multiselectable="true">
                                        <div className="panel panel-col-blue-grey">
                                            <div className="panel-heading" role="tab" id="headingThree_19">
                                                <h4 className="panel-title">
                                                    <a className="collapsed" role="button" data-toggle="collapse" href="#collapseThree_19" aria-expanded="false" aria-controls="collapseThree_19">
                                                        <i className="material-icons">contact_phone</i> Details
                                                    </a>
                                                </h4>
                                            </div>
                                            <div id="collapseThree_19" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingThree_19">
                                                <div className="panel-body">
                                                    <h4>Contact M2Cars</h4>
                                                    <p>For More Details About This Vehicle </p>
                                                    <a href="tel:+19549999940" type="button" className="btn btn-block btn-lg btn-success waves-effect">
                                                        <i className="material-icons">phone</i>
                                                        <span>PHONE</span>
                                                    </a>
                                                    <a href="tel:+19549999940" type="button" className="btn btn-block btn-lg bg-light-green waves-effect">
                                                        <i className="material-icons">chat</i>
                                                        <span>WHATSAPP</span>
                                                    </a>
                                                    <a href="https://www.facebook.com/m2carspremium/" rel="noopener noreferrer" target="_blank" type="button" className="btn btn-block btn-lg bg-indigo waves-effect">
                                                        <i className="material-icons">thumb_up</i>
                                                        <span>FACEBOOK</span>
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="panel panel-col-blue-grey">
                                            <div className="panel-heading" role="tab" id="headingTwo_19">
                                                <h4 className="panel-title">
                                                    <a className="collapsed" role="button" data-toggle="collapse" href="#collapseTwo_19" aria-expanded="true" aria-controls="collapseTwo_19">
                                                        <i className="material-icons">directions_car</i> Vehicle Description
                                                    </a>
                                                </h4>
                                            </div>
                                            <div id="collapseTwo_19" className="panel-collapse collapse in" role="tabpanel" aria-labelledby="headingTwo_19">
                                                <div className="panel-body">
                                                    <ul className="list-group">
                                                        <li className="list-group-item"><strong>Year: </strong>{this.state.car.year}</li>
                                                        <li className="list-group-item"><strong>Make: </strong>{this.state.car.make}</li>
                                                        <li className="list-group-item"><strong>Model: </strong>{this.state.car.model}</li>
                                                        <li className="list-group-item"><strong>Trim: </strong>{`${this.state.car.trim}`}</li>
                                                        <li className="list-group-item"><strong>Mileage: </strong>{this.state.car.mileage}</li>
                                                        <li className="list-group-item"><strong>Trans: </strong>{this.state.car.trans}</li>
                                                        <li className="list-group-item"><strong>Vin: </strong>{this.state.car.vin}</li>
                                                        <li className="list-group-item"><strong>Stock: </strong>{this.state.car.stock}</li>
                                                        <li className="list-group-item"><strong>Engine: </strong>{this.state.car.engine}</li>
                                                        <li className="list-group-item"><strong>Color: </strong>{this.state.car.color}</li>
                                                        <li className="list-group-item"><strong>Drive train: </strong>{this.state.car.drive_train}</li>
                                                        <li className="list-group-item"><strong>Type: </strong>{this.state.car.type}</li>
                                                        <li className="list-group-item"><strong>State: </strong>{this.state.car.state}</li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="panel panel-col-blue-grey">
                                            <div className="panel-heading" role="tab" id="headingOne_19">
                                                <h4 className="panel-title">
                                                    <a role="button" data-toggle="collapse" href="#collapseOne_19" aria-expanded="true" aria-controls="collapseOne_19">
                                                        <i className="material-icons">assignment</i> Options
                                                </a>
                                                </h4>
                                            </div>
                                            <div id="collapseOne_19" className="panel-collapse collapse in" role="tabpanel" aria-labelledby="headingOne_19">
                                                <div className="panel-body">
                                                    <ul>
                                                        {this.renderOptions(this.state.car._options)}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}



