import React, { Component } from 'react';
import CardCar from './cardcar/CardCar';
import consts from '../../../consts';
import axios from 'axios';

const BASE_URL = consts.API_URL;

class CarList extends Component {

    constructor(props) {
        super(props)
        this.state = {
            vehicles: [],
            vehiclesFiles: [],
            cards: [],
        }
    }

    getVehicles() {
        axios.get(`${BASE_URL}/vehicles`)
            .then(res => {
                this.setState({
                    vehicles: res.data
                })
                this.getVehiclesFiles();
            })
    }

    getVehiclesFiles() {
        this.state.vehicles.map((vehicle, index) => (
            axios.get(`${BASE_URL}/containers/${vehicle.id}/files`)
                .then(res => {
                    vehicle = { ...vehicle, imgs: res.data }
                    this.setState({
                        vehiclesFiles: [...this.state.vehiclesFiles, vehicle]
                    })
                    this.renderCardsWithImg();
                })
        ))
    }


    renderCardsWithImg() {
        this.setState({
            cards: this.state.vehiclesFiles.map((car, index) => (
                <CardCar key={index} id={car.id} img={`${BASE_URL}/containers/${car.id}/download/${car.imgs[0].filename}`} stars={car.stars} model={car.model} year={car.year} value={car.price} />
            ))
        })
    }

    componentWillMount() {
        this.getVehicles();
    }


    render() {
        return (
            <div>
                {this.state.cards}
            </div>
        )
    }
}

export default CarList;



