import React from 'react';

import Logo from '../../../assets/logo/m2cars.png';

export default () => {
    return (
        <li>
            <div className="user-info">
                <div className="image">
                    <img src={Logo} width="150" height="150" alt="User" />
                </div>
            </div>
        </li>
    )
}



