import React from 'react';

import MenuItem from './menuitem/MenuItem';
import UserInfo from './userinfo/UserInfo';
import FooterSideBar from './footersidebar/FooterSideBar';

export default () => {
    return (
        <section>
            <aside id="leftsidebar" className="sidebar">
                <div className="menu">
                    <ul className="list">                        
                        <UserInfo />
                        {/* <li className="header">Menu</li> */}
                        <MenuItem link='/' icon='home' label='Home' />
                        <MenuItem link='/about' icon='info' label='About' />
                        {/* <MenuItem icon='cached' label='Sub-Menu' >
                            <MenuItem link='/pages' label='Pages' />
                        </MenuItem> */}
                    </ul>
                </div>
                <FooterSideBar />
            </aside>
        </section>
    )
}



